.navbar {
  position: relative;
  min-height: 90px;
  margin-bottom: 20px;
  background-color: #ffffff;
  border: 1px solid transparent;
}

.navbar-linkquidator {
  padding: 20px 0;
  border-bottom: 0;
  background-color: transparent;
  position: absolute;
  left: 0;
  right: 0;
  margin-bottom: 0;
}

.navbar-linkquidator .navbar {
  background-color: transparent;
}

.navbar-linkquidator .navbar-toggle {
  background-color: #ffffff;
}

.navbar-linkquidator .navbar-toggle .icon-bar {
  background-color: #2D86D0;
}

.navbar-linkquidator .navbar-nav > .active > a, .navbar-linkquidator .navbar-default .navbar-nav > .active > a:focus, .navbar-linkquidator .navbar-default .navbar-nav > .active > a:hover {
  color: #ffffff;
  background-color: transparent;
}

.navbar-linkquidator .navbar-nav > .active > a:after, .navbar-linkquidator .navbar-default .navbar-nav > .active > a:focus:after, .navbar-linkquidator .navbar-default .navbar-nav > .active > a:hover:after {
  content: '';
  width: 25px;
  height: 2px;
  left: 15px;
  bottom: 10px;
  position: absolute;
  background-color: #fff;
}

.navbar-linkquidator .navbar-nav > li > a {
  color: #ffffff;
  text-transform: uppercase;
  font-size: 14px;
}

.navbar-linkquidator .navbar-nav > li > a:hover {
  color: #ffffff;
  background-color: transparent;
}

.navbar-linkquidator .navbar-nav > li > a:hover:after {
  -webkit-transition: all ease 0.3s;
  /* Saf3.2+, Chrome */
  -moz-transition: all ease 0.3s;
  /* FF4+ */
  -ms-transition: all ease 0.3s;
  /* IE10? */
  -o-transition: all ease 0.3s;
  /* Opera 10.5+ */
  transition: all ease 0.3s;
  content: '';
  width: 25px;
  height: 2px;
  left: 15px;
  bottom: 10px;
  position: absolute;
  background-color: #1d3e6c;
}

.navbar-linkquidator .navbar-nav.navbar-right > li > a {
  color: #ffffff;
  background-color: transparent;
  border: 2px solid #ffffff;
  border-radius: 27px;
  padding: 8px 31px;
  margin-right: 14px;
  margin-top: 4px;
  text-transform: capitalize;
  -webkit-transition: all ease 0.3s;
  /* Saf3.2+, Chrome */
  -moz-transition: all ease 0.3s;
  /* FF4+ */
  -ms-transition: all ease 0.3s;
  /* IE10? */
  -o-transition: all ease 0.3s;
  /* Opera 10.5+ */
  transition: all ease 0.3s;
}

.navbar-linkquidator .navbar-nav.navbar-right > li > a:hover {
  background-color: #ffffff;
  color: #1d3e6c;
  -webkit-transition: all ease 0.3s;
  /* Saf3.2+, Chrome */
  -moz-transition: all ease 0.3s;
  /* FF4+ */
  -ms-transition: all ease 0.3s;
  /* IE10? */
  -o-transition: all ease 0.3s;
  /* Opera 10.5+ */
  transition: all ease 0.3s;
}

.navbar-linkquidator .navbar-nav.navbar-right > li > a:hover:after {
  display: none;
}

